* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
}

img, video {
  display: block;
}

body, html {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

h1 {
  text-align: center;
}

.section-bd {
  background-color: #000;
  border-top: 2px solid #e0e0e0;
  margin: -2px;
  padding: 60px 40px;
}

.bd-container {
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.bd-image {
  flex: 40%;
  padding-right: 20px;
}

.bd-image img {
  width: 100%;
  height: auto;
  box-shadow: 0 4px 8px #0000001a;
}

.bd-content {
  flex: 60%;
  padding-left: 20px;
}

.bd-title {
  color: var(--white, #fff);
  font-family: Bokor;
  font-size: 80px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.bd-description {
  color: var(--white, #fff);
  padding-top: 42px;
  padding-bottom: 42px;
  font-family: Bricolage Grotesque;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.bd-isbn {
  color: var(--white, #fff);
  padding-bottom: 42px;
  font-family: Bricolage Grotesque;
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.buy-button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  text-align: center;
  z-index: 2;
  background-color: #ff4747;
  border: none;
  flex-shrink: 0;
  width: 267px;
  height: 83px;
  padding: 10px 20px;
  font-family: Bokor, system-ui;
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  text-decoration: none;
  bottom: 20px;
  right: 20px;
}

.buy-button:hover {
  color: #ff5733;
  background-color: #fff;
}

header {
  text-align: center;
  color: #fff;
  position: relative;
}

.header-background, .header-logo {
  width: 100%;
  height: auto;
  display: block;
}

.header-content {
  text-align: left;
  position: absolute;
  bottom: 58px;
  left: 201px;
}

.furieuse-logo {
  fill: #fff;
  width: 300px;
  height: auto;
  margin-bottom: 10px;
}

h1 {
  text-transform: uppercase;
  font-family: Bokor, system-ui;
  font-size: 3rem;
}

.cta-button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  text-align: center;
  z-index: 2;
  background-color: #ff4747;
  border: none;
  padding: 10px 20px;
  font-family: Bokor, system-ui;
  font-size: 1.2rem;
  font-style: normal;
  font-weight: 400;
  text-decoration: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.cta-button:hover {
  color: #ff4747;
  background-color: #fff;
}

.subtitle {
  color: #fff;
  z-index: 1;
  margin-top: 10px;
  font-size: 1rem;
  position: relative;
}

footer {
  color: var(--white, #fff);
  z-index: 20000;
  background: #000;
  justify-content: space-between;
  align-items: flex-start;
  padding: 100px;
  font-family: Bricolage Grotesque;
  font-size: 25px;
  font-weight: 400;
  display: flex;
  position: relative;
}

.footer-droit {
  flex-direction: column;
  align-items: flex-end;
  gap: 15px;
  padding-top: 195px;
  display: flex;
}

.footer a {
  text-decoration: none;
}

.footer-logo {
  height: 30px;
}

.politique {
  padding-top: 20px;
}

.medias {
  gap: 10px;
  padding-top: 20px;
  display: flex;
}

.medias img {
  width: 50px;
  height: 50px;
}

.footer-gauche {
  flex-direction: column;
  align-items: flex-start;
  gap: 40px;
  display: flex;
}

.container, .container-fluid, .container-xxl, .container-xl, .container-lg, .container-md, .container-sm {
  --bs-gutter-x: 1.5rem;
  --bs-gutter-y: 0;
  padding-right: calc(var(--bs-gutter-x) * .5);
  padding-left: calc(var(--bs-gutter-x) * .5);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media (width >= 576px) {
  .container-sm, .container {
    max-width: 540px;
  }
}

@media (width >= 768px) {
  .container-md, .container-sm, .container {
    max-width: 720px;
  }
}

@media (width >= 992px) {
  .container-lg, .container-md, .container-sm, .container {
    max-width: 960px;
  }
}

@media (width >= 1200px) {
  .container-xl, .container-lg, .container-md, .container-sm, .container {
    max-width: 1140px;
  }
}

@media (width >= 1400px) {
  .container-xxl, .container-xl, .container-lg, .container-md, .container-sm, .container {
    max-width: 1320px;
  }
}

